<template>
    <v-container fluid>
        <v-layout column>
        <v-flex><!-- plyer list view / start-->
        <v-row>
          <v-col></v-col>
          <v-col v-for="(mem,i) in members" :key="mem" >
            <v-img v-if="i===player" alt="player" class="shrink mr-2" contain src="../../assets/sejong1.png"
                transition="scale-transition" width="30"/>
            <v-img v-else alt="player" class="shrink mr-2" contain src="../../assets/sejong0.png"
                transition="scale-transition" width="30"/><p>{{mem}}</p>
          </v-col>
          <v-col></v-col>
        </v-row>
      </v-flex><!-- plyer list view / end-->
        
        <v-layout wrap align-center column v-if="isGameOn">
            <v-flex text-xs-center>
            <h1> : {{chosung}}</h1>
            </v-flex>
            <br/><br/>
            <v-flex>
                <v-text-field v-model="word" label="   " required @keyup.enter.exact = "OnClickNext"></v-text-field>
            </v-flex><!--   -->
            <br/>
            <v-flex>
            <p>   : </p>
            <v-card><v-card-text>{{dictionary}}</v-card-text></v-card>
            </v-flex>
        </v-layout>
        <v-layout justify-center v-if="!isGameOn">
            <v-flex x1></v-flex>
            <v-flex>
                <v-text-field v-model="chosung" label="      " required @keyup.enter.exact = "SetChosung"></v-text-field>
            </v-flex>
            <v-flex x1></v-flex>
        </v-layout>
        <v-layout v-show="isGameEnded" row>
        <v-flex><h1>{{lastPlayer()}} :  !</h1></v-flex>
        <v-flex><v-btn @click="OnClickReset"> </v-btn></v-flex>
        <v-flex><h2>      </h2></v-flex>
        </v-layout>
        </v-layout>
    </v-container>
</template>

<script>
import axios from 'axios'
import cheerio from 'cheerio'

export default {
    data(){
        return{
            isGameOn : false,
            isGameEnded: false,
            chosung:"",
            word:"",
            words:[],
            dictionary:"",
            player:0,
            hangeulCheck : /[-|-|-]/,
            chosungs : [ "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "" ],
            dictUrl :"https://opendict.korean.go.kr/search/searchResult?focus_name=query&query="
        }
    },
    props:{
        members:{
            type:Array,
            default:[]
        }
    },
    methods:{
        SetChosung(){
            if(this.hangeulCheck.test(this.chosung.trim())){
                if(this.chosung.trim().length==2){
                    this.chosung = this.MakeChosung()
                    if(this.chosung) this.isGameOn=true
                    else {
                        this.chosung=""
                        alert("    .  .")
                    }
                }else{
                    alert("     !")
                }
            }else{
                alert("  !")
            }
        },
        MakeChosung(){
            let a = this.chosung.charCodeAt(0)-0xAC00; 
		    let b = this.chosung.charCodeAt(1)-0xAC00; 
		    let a_loc = parseInt((a/28)/21); 
            let b_loc = parseInt((b/28)/21);
            if(this.chosungs[a_loc] && this.chosungs[b_loc]) return this.chosungs[a_loc] + this.chosungs[b_loc]
             
        },
        OnClickNext(){
            let result = this.searchDict(this.word)
            result===true? this.turnNext():endGame()  
        },
        turnNext(){
            this.player++;
            if(this.player===this.members.length) this.player=0;   
        },
        searchDict(word){
            console.log(word)
            axios.get("http://localhost:8080/dictApi"+word)
                .then(r=>{
                    const $ = cheerio.load(r.data)
                    console.log($("body > #wrap > .container > #contentWrap > #content > .cont_box_lr"+
                    "> #searchPaging > .floatL > .mt30 > .panel > li").html()) // children : result_none or search_result
  
                })
                .catch(e=>{
                    console.log(e + "  ")
                })

        },
        lastPlayer(){
            return this.members[this.player]
        },
        OnClickReset(){
            this.player=0
            this.chosung=""
            this.word=""
            this.words=[]
            this.dictionary=""
            this.isGameEnded=false
            this.isGameOn=false
        }

    },
    mounted(){

    }
    
}
</script>

